"use client"

import { useState, useRef } from 'react';
import { DraggableItem } from '@/components/atoms/DraggableItem';
import DropZone from '@/components/atoms/DropZone';
import GameHeader from '@/components/molecules/GameHeader';
import { Brain, Target, Zap } from 'lucide-react';
import { useToast } from "@/hooks/use-toast";
import BackgroundTemplate from '@/components/templates/GamesTemplate';

interface GameItem {
  id: string;
  name: string;
  category: string;
  icon: string;
  color: string;
}

interface GameSet {
  id: string;
  name: string;
  color: string;
  description: string;
  icon: any;
}

export default function Page() {
  const { toast } = useToast();

  // Items relacionados con neurociencia cognitiva
  const gameItems: GameItem[] = [
    { id: '1', name: 'Neurona', category: 'células', icon: '🧠', color: 'bg-blue-100' },
    { id: '2', name: 'Sinapsis', category: 'células', icon: '⚡', color: 'bg-blue-100' },
    { id: '3', name: 'Dendrita', category: 'células', icon: '🌿', color: 'bg-blue-100' },
    { id: '4', name: 'Memoria', category: 'procesos', icon: '💭', color: 'bg-green-100' },
    { id: '5', name: 'Atención', category: 'procesos', icon: '👁️', color: 'bg-green-100' },
    { id: '6', name: 'Aprendizaje', category: 'procesos', icon: '📚', color: 'bg-green-100' },
    { id: '7', name: 'Dopamina', category: 'neurotransmisores', icon: '🧪', color: 'bg-purple-100' },
    { id: '8', name: 'Serotonina', category: 'neurotransmisores', icon: '💊', color: 'bg-purple-100' },
    { id: '9', name: 'Acetilcolina', category: 'neurotransmisores', icon: '⚗️', color: 'bg-purple-100' },
  ];

  const gameSets: GameSet[] = [
    {
      id: 'células',
      name: 'Células Nerviosas',
      color: 'border-blue-400 bg-blue-50',
      description: 'Componentes básicos del sistema nervioso',
      icon: Brain
    },
    {
      id: 'procesos',
      name: 'Procesos Cognitivos',
      color: 'border-green-400 bg-green-50',
      description: 'Funciones mentales superiores',
      icon: Target
    },
    {
      id: 'neurotransmisores',
      name: 'Neurotransmisores',
      color: 'border-purple-400 bg-purple-50',
      description: 'Mensajeros químicos del cerebro',
      icon: Zap
    },
  ];

  const [items, setItems] = useState(gameItems);
  const [score, setScore] = useState(0);
  const [completedSets, setCompletedSets] = useState<string[]>([]);
  const dragItem = useRef<GameItem | null>(null);

  const handleDragStart = (item: GameItem) => {
    dragItem.current = item;
    console.log('Iniciando arrastre:', item.name);
  };

  const handleDrop = (setId: string) => {
    if (!dragItem.current) return;

    const item = dragItem.current;
    console.log('Soltando:', item.name, 'en conjunto:', setId);

    if (item.category === setId) {
      // Clasificación correcta
      setItems(prev => prev.filter(i => i.id !== item.id));
      setScore(prev => prev + 10);

      toast({
        title: "¡Excelente conexión neural! 🧠",
        description: `${item.name} pertenece a ${gameSets.find(s => s.id === setId)?.name}`,
        duration: 2000,
      });

      // Verificar si el conjunto está completo
      const remainingItemsInSet = items.filter(i => i.category === setId && i.id !== item.id);
      if (remainingItemsInSet.length === 0) {
        setCompletedSets(prev => [...prev, setId]);
        toast({
          title: "¡Conjunto completado! 🎉",
          description: "Has activado todas las conexiones sinápticas",
          duration: 3000,
        });
      }
    } else {
      // Clasificación incorrecta
      toast({
        title: "Conexión sináptica interrumpida",
        description: "Intenta con otro conjunto neural",
        duration: 2000,
        variant: "destructive"
      });
    }

    dragItem.current = null;
  };

  const isGameComplete = completedSets.length === gameSets.length;

  return (
    <BackgroundTemplate>
        <div className="max-w-6xl mx-auto">
          <GameHeader score={score} completedSets={completedSets.length} totalSets={gameSets.length} />

          {isGameComplete ? (
            <div className="text-center py-16 gentle-float">
              <div className="bg-white rounded-3xl p-8 shadow-2xl border border-green-200">
                <div className="text-6xl mb-4">🧠✨</div>
                <h2 className="text-4xl font-bold text-green-600 mb-4">¡Red Neural Completada!</h2>
                <p className="text-xl text-gray-600 mb-6">
                  Has establecido todas las conexiones sinápticas correctamente
                </p>
                <div className="text-3xl font-bold text-blue-600">
                  Puntuación Final: {score} puntos
                </div>
              </div>
            </div>
          ) : (
            <div className="grid grid-cols-1 lg:grid-cols-2 gap-8">
              {/* Área de elementos arrastrables */}
              <div className="space-y-6">
                <div className="bg-white rounded-2xl p-6 shadow-lg border border-gray-200">
                  <h2 className="text-2xl font-bold text-gray-800 mb-4 flex items-center gap-2">
                    <Brain className="text-blue-500" />
                    Elementos Neurocientíficos
                  </h2>
                  <p className="text-gray-600 mb-6">
                    Arrastra cada elemento al conjunto correspondiente para activar las conexiones sinápticas
                  </p>
                  <div className="grid grid-cols-2 sm:grid-cols-3 gap-4">
                    {items.map((item) => (
                      <DraggableItem
                        key={item.id}
                        item={item}
                        onDragStart={handleDragStart}
                      />
                    ))}
                  </div>
                </div>
              </div>

              {/* Área de conjuntos */}
              <div className="space-y-4">
                <h2 className="text-2xl font-bold text-gray-800 flex items-center gap-2">
                  <Target className="text-green-500" />
                  Conjuntos Neuronales
                </h2>
                {gameSets.map((set) => (
                  <DropZone
                    key={set.id}
                    set={set}
                    onDrop={handleDrop}
                    isCompleted={completedSets.includes(set.id)}
                    itemCount={items.filter(item => item.category === set.id).length}
                  />
                ))}
              </div>
            </div>
          )}
        </div>
    </BackgroundTemplate>
  );
};

